<template>
  <div id="home" >
    <nav-bar class="home-nav">
      <div slot="center">购物街</div>
      </nav-bar>
         <tab-control  id='scop'
                        :titles="['流行', '新款', '精选']"
                        @tabClick="tabClick" 
                        ref="TabControl1"                       
                        class='TabControl'
                        v-show="isTabFixed"
                        />
          <scroll class="content"
                ref="scroll"
                :probe-type="3"
                @scroll="contentScroll"
                :pull-up-load="true"           
                > 
            <!-- @pullingUp="loadMore" -->
          
       <home-swiper :banners="banners" @swiperImageLoad='swiperImageLoad' />
        <recommend-view :recommends="recommends"/>
         <feature-view/>
           <tab-control id='scop'
                        :titles="['流行', '新款', '精选']"
                        @tabClick="tabClick" 
                        ref="TabControl2"                      
                        />
          <good-list :goods="showGoods"/>
           </scroll>
      <back-top @click.native="backClick" v-show="isShowBackTop"/> 
    <!--<div>呵呵呵呵</div>-->  
  </div>
</template>

<script>
   import HomeSwiper from './childComps/HomeSwiper'
   import RecommendView from './childComps/RecommendView'
   import FeatureView from './childComps/FeatureView'

   import NavBar from 'components/common/navbar/NavBar'
   import TabControl from 'components/content/tabControl/TabControl'
   import GoodList from 'components/content/goods/GoodsList'
   import Scroll from 'components/common/scroll/Scroll'
   import BackTop from 'components/content/backTop/BackTop'

   import { getHomeMultidata ,getHomeGoods } from "network/home" 
   import {debounce} from "common/utils" 

  export default {
    name: "Home",
    components: {
       HomeSwiper,
         RecommendView,
         FeatureView,
         NavBar,
         TabControl,
         GoodList,
         Scroll,
         BackTop
    },
    data() {
      return {
        banners: [],
        recommends: [],
        goods: {
          'pop': {page: 0, list: []},
          'new': {page: 0, list: []},
          'sell': {page: 0, list: []},
        },
        currentType: 'pop',
        isShowBackTop: false,
        tapOffsetTop:0,
        isTabFixed:false,
        saveY:0
        
      }
    },
   
    computed: {
      showGoods() {
        return this.goods[this.currentType].list
      }
    },

    //keep-alive  解决频繁切换导致的bug
    activated() {
      this.$refs.scroll.scrollTo(0,this.saveY,0)
    },
    deactivated() {
      this.saveY=this.$refs.scroll.getScrolly()
    },


     created() {
    //   // 1.请求多个数据
     this.getHomeMultidata();

      // 2.请求商品数据
      this.getHomeGoods('pop')
      this.getHomeGoods('new')
      this.getHomeGoods('sell') 

      
     },
       mounted: function () {
       // window.addEventListener('scroll', this.handleScroll, true);  // 监听（绑定）滚轮滚动事件
       
       //防抖函数
       const refresh=debounce(this.$refs.scroll.refresh,50)
       //3.监听item中图片加载完成  
      this.$bus.$on('itemImageLoad',()=>{
             refresh()                
      })
    

      },
     methods: {
    //   /**
    //    * 事件监听相关的方法
    //    */
    
      tabClick(index) {
        console.log(index)
        switch (index) {
          case 0:
            this.currentType = 'pop'
            break
          case 1:
            this.currentType = 'new'
            break
          case 2:
            this.currentType = 'sell'
            break
        }
        //确定两个tab-control在切换过程中保持相等
        this.$refs.TabControl1.currentIndex=index;
        this.$refs.TabControl2.currentIndex=index;
      },
      backClick() {
        this.$refs.scroll.scrollTo(0, 0)
      },
      contentScroll(position) {
        //1.判断backTop是否显示
        this.isShowBackTop = (-position.y) > 1000
        //2.决定tabControl是否吸顶(position:fixed)
        this.isTabFixed=(-position.y) > this.tapOffsetTop
      },
      loadMore() {
        this.getHomeGoods(this.currentType)
      },
      swiperImageLoad(){
      // 吸顶效果
      this.tapOffsetTop=this.$refs.TabControl2.$el.offsetTop-5
      console.log(this.$refs.TabControl2.$el.offsetTop)
      },
    //   /**
    //    * 网络请求相关的方法
    //    */
      getHomeMultidata() {
        getHomeMultidata().then(res => {
          // this.result = res;
          this.banners = res.data.banner.list;
          this.recommends = res.data.recommend.list;
        })
      },
      getHomeGoods(type) {
        const page = this.goods[type].page + 1
        getHomeGoods(type, page).then(res => {
          this.goods[type].list.push(...res.data.list)
          this.goods[type].page += 1

        //  this.$refs.scroll.finishPullUp()  //加载更多一次后 需要执行这个方法之后，才能继续加载

        })
       },
      
    }
  }
</script>

