    /*padding-top: 44px;*/
    height: 100vh;
    position: relative;
  }

  .home-nav {
    
    color: #fff;
    background-color:pink;
    /* position: fixed;  
    left: 0;
    right: 0;
    top: 0;
    z-index: 9; */
  }

  .tab-control {
    position:relative;
    z-index: 10;
  }

  .content {
    overflow: hidden;
    position: absolute;
    top: 44px;
    bottom: 49px;
    left: 0;
    right: 0;
  }
  .fixed{
    position: fixed;
    left: 0;
    right: 0;
    top: 44px
  }

